import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Groups from './panels/Group';
import Auth from './panels/Auth';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			grouptoken: null,
			groupids: null,
			userid: null,
			fetchedGroup: null,
			token: 'b22bbd168f3ac920a04aafc823ad9cc6086a7204dd3d53d312dadbfd87f818553509702ab9cab1bf0d41c',
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data, userid: e.detail.data.id });
					console.log(e.detail.data.id);
					console.log(e.detail.data);
					break;
				default:
					console.log(e.detail.type);
					// console.log('тут детали', e.detail);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	installApp = (e) => {
		console.log('Главный',e)
		
		connect.send("VKWebAppAddToCommunity", {})

		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAddToCommunityResult':
					this.setState({ activePanel: 'groups', groupids: e.detail.data.group_id });
					console.log('тут детали', e.detail.data.group_id);
					break;
				default:
					console.log(e.detail.type);
					
			}
		});
		
	}
	authApp = (e) => {
		console.log('Главный',e)
		
		connect.send("VKWebAppGetAuthToken", {"app_id": 7047197, "scope": "groups,stats"})

		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAccessTokenReceived':
					this.setState({ activePanel: 'groups', grouptoken: e.detail.data.access_token });
					console.log('тут детали', e.detail.data.access_token);
					break;
				default:
					console.log(e.detail.type);
					
			}
		});
		
	}

	getapi = (e) => {
		connect.send("VKWebAppCallAPIMethod", {"method": "groups.getById", "params": {"group_id": this.groupids, "v":"5.101", "access_token": this.grouptoken}});
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppCallAPIMethodResult':
					this.setState({ activePanel: 'groups', fetchedGroup: e.detail.data });
					console.log('тут детали', e.detail.data);
					break;
				default:
					console.log(this.groupids);
					console.log(this.grouptoken);
					console.log(e.detail.type);
					console.log(e.detail);
					
			}
		});
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Auth id="auth" fetchedUser={this.state.fetchedUser} authApp={this.authApp}/>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} installApp={this.installApp} />
				<Persik id="persik" go={this.go} />
				<Groups id="groups" 
						go={this.go} 
						fetchedUser={this.state.fetchedUser} 
						authApp={this.authApp}
						installApp={this.installApp}
						grouptoken={this.state.grouptoken}
						groupids={this.state.groupids}
						fetchedGroup={this.state.fetchedGroup}
						getapi={this.getapi}
						/>
			</View>
		);
	}
}

export default App;
