import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, Group, Button, ListItem, Avatar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import connect from '@vkontakte/vkui-connect';
import Auth from './Auth';

const osname = platform();




const Groups = ({id, installApp, authApp, grouptoken, groupids, getapi, fetchedGroup}) => (
    
        <Panel id={id}>
		<PanelHeader>
			Управление
		</PanelHeader>
        
        {grouptoken === null && 
		<Group title="Отображения Групп">
		<Div>
			<Button size="xl" level="2" onClick={authApp} data-to="persik">
				Разрешить
			</Button>
		</Div>
		</Group>}

		{ grouptoken &&
			<Group title="Отображения Групп">
			<Div>
				{groupids}
				{fetchedGroup !== null &&
				<ListItem
					before={fetchedGroup.photo_200 ? <Avatar src={fetchedGroup.photo_200}/> : null}
					description={fetchedGroup.id }
				>
					{`${fetchedGroup.name}`}
				</ListItem>}
			</Div>
			<Div>
			<Button size="xl" level="3" onClick={installApp} data-to="instal-after-token">
				Установить В другое сообщество!
			</Button>
		</Div>
		
		</Group>}
		
		<Group title='Проверка'>
            <Div>
			<Button size="xl" level="2" onClick={getapi} data-to="get-api">
				Проверить
			</Button>
			</Div> 
        </Group>
	
        <Group title={`Настройка Виджета в группе: ${groupids}`}>
            <Div>тут делаем все по группе</Div> 
        </Group>
	</Panel>
    
    
	
);

 
Groups.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Groups;